<template>
  <ul @click.prevent="dblclck()" id="sidebar">
    <li
      v-for="(cat, i) in this.categories"
      :key="i"
      @click.prevent="pickCategory(cat)"
    >
      <span ref="side">{{ cat.strCategory }}</span>
    </li>
  </ul>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      categories: [],
      categoryLink: "www.thecocktaildb.com/api/json/v1/1/filter.php?c=",
    };
  },
  methods: {
    async dblclck() {
      await event.target.click()
    },
    async pickCategory(category) {
      await this.$store.dispatch("pickCategory", category);
      this.$refs.side.forEach(el => {
        // console.log(el.innerText === category.strCategory, el.innerText, category.strCategory)
        el.innerText === category.strCategory 
          ? el.classList.add("active-side")
          : el.classList.remove("active-side")
      })
      await this.$router.push("/category-items").catch(() => {});
    },
  },
  created: async function () {
    axios
      .get("https://www.thecocktaildb.com/api/json/v1/1/list.php?c=list")
      .then((res) => (this.categories = res.data.drinks))
      .catch((err) => console.log(err));
  },
};
</script>

<style lang="scss" scoped>
@import "../scss/mixins.scss";
ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 5px 10px;
  li {
    @include base-font($base-color);
    font-size: 24px;
    padding: 10px 5px;
    user-select: none;
    cursor: pointer;
    // transition: transform 0.1s ease-in-out;
    .active-side {
      animation: flicker .5s infinite alternate;
      // border: 2px solid #fff;
      padding: 5px;
      // border-radius: .5rem;
    }
  }
  @keyframes flicker {
    
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
  
        text-shadow:
        0 0 4px #fff,
        0 0 11px #fff,
        0 0 19px #fff,
        0 0 40px $base-color,
        0 0 80px $base-color,
        0 0 90px $base-color,
        0 0 100px $base-color,
        0 0 150px $base-color;
    
    }
    
    20%, 24%, 55% {        
        text-shadow: none;
    }    
  }
}
</style>