import axios from "axios";
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    pickedList: null,
    pickedCard: null,
    pickedIngr: null,
    searchRes: null,
  },
  mutations: {
    pickCategory(state, payload) {
      const link = `https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=${payload.strCategory
        .split(" ")
        .join("_")}`;
      axios.get(link).then((res) => (state.pickedList = res.data));
    },
    getDrinkCard(state, payload) {
      const link = `https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i=${payload}`;
      axios.get(link).then((res) => (state.pickedCard = res.data.drinks[0]));
    },
    getIngrCard(state, payload) {
      const link = `https://www.thecocktaildb.com/api/json/v1/1/search.php?i=${payload
        .split(" ")
        .map((s) => s.charAt(0).toUpperCase() + s.slice(1))
        .join("_")}`;
      axios
        .get(link)
        .then((res) => (state.pickedIngr = res.data.ingredients[0]));
    },
    goSearch(state, payload) {
      const link = `https://www.thecocktaildb.com/api/json/v1/1/search.php?s=${payload}`;
      axios.get(link).then((res) => (state.searchRes = res.data.drinks[0]));
    },
  },
  actions: {
    async pickCategory(context, payload) {
      return await context.commit("pickCategory", payload);
    },
    async getDrinkCard(context, payload) {
      return await context.commit("getDrinkCard", payload);
    },
    async getIngrCard(context, payload) {
      return await context.commit("getIngrCard", payload);
    },
    async goSearch(context, payload) {
      return await context.commit("goSearch", payload);
    },
  },
  getters: {
    // getPickedCategory: (state) => {
    //   return state.pickedCategory;
    // },
    getPickedList: (state) => {
      return state.pickedList;
    },
    getCardInfo: (state) => {
      return state.pickedCard;
    },
    getIngrInfo: (state) => {
      return state.pickedIngr;
    },
  },
  modules: {},
});
