<template>
  <div class="header">
    <!-- <div @click.prevent="goHome">
      <img src="@/assets/cocktail.svg" class="header__logo" alt="Cocktail" />
    </div> -->
    <h3 class="header__title" @click.prevent="goHome">bar at home</h3>
    <form class="header__search">
      <input type="text" class="header__search-input" v-model="search" />
      <button type="submit" class="header__search-button" @click.prevent="goSearch"></button>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
    };
  },
  methods: {
    goSearch() {
      return this.$store.dispatch("goSearch", this.search)
    },
    goHome() {
      this.$parent.$children
        .filter((el) => Object.keys(el.$refs).length)[0]
        .$refs.side.forEach((s) => s.classList.remove("active-side"));
      this.$router.push("/").catch(() => {});
    },
  },
};
</script>


<style lang="scss" scoped>
@import "../scss/mixins.scss";
.header {
  height: 70px;
  padding: 5px 40px;
  // display: flex;
  // justify-content: space-between;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  position: sticky;
  top: 0;
  @include neon-border($base-color);
  &__logo {
    cursor: pointer;
    fill: #b70deb;
  }
  &__title {
    cursor: pointer;
    letter-spacing: 0.5rem;
    color: #fff;
    text-transform: uppercase;
    font-size: 36px;
    animation: pulsate 2.5s infinite alternate;
    text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #b70deb,
      0 0 82px #b70deb, 0 0 92px #b70deb, 0 0 102px #b70deb, 0 0 151px #b70deb;
  }
  &__search {
    display: flex;
    align-items: center;
    justify-self: end;

    &-input {
      background: rgb(78, 78, 78);
      border: none;
      border-radius: 3px;
      // color: $base-color;
      margin-right: 5px;
      @include base-font($base-color);
      @include neon-border(fuchsia);
      animation: pulsate 2.5s infinite alternate;
      &:focus {
        outline: none;
        background: rgb(78, 78, 78);
      }
    }
    &-button {
      background: url("../assets/search.svg") transparent no-repeat center;
      height: 30px;
      width: 30px;
      border: none;
      &:hover {
        transform: scale(1.1);
      }
    }
  }
  @keyframes pulsate {
    100% {
      /* Larger blur radius */
      text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #b70deb,
        0 0 80px #b70deb, 0 0 90px #b70deb, 0 0 100px #b70deb, 0 0 150px #b70deb;
    }
    0% {
      /* Smaller blur radius */
      text-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #fff, 0 0 10px #b70deb,
        0 0 45px #b70deb, 0 0 55px #b70deb, 0 0 70px #b70deb, 0 0 80px #b70deb;
    }
  }
}
</style>
